.sidebar-toggle {
  font-size: 16px;
  text-align: center;
  float: left;
  padding-top: 3px;
  overflow: hidden;
  height: 100%;
}
.sidebar-toggle.sidebar-open i {
  transform: rotate(90deg);
}
.sidebar-toggle i {
  transition: transform 0.5s;
  color: white;
  font-size: 20px;
}
.nav-header {
  background-color: #fff;
  color: black;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9;
  border: 1px solid #E6E6E6;
  height: 60px;
}
.nav-header:after {
  content: ' ';
  clear: both;
  display: block;
}
.nav-header .user-profile {
  color: #909399;
  height: 20px;
  width: 20px;
}
.nav-header .user-profile img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.nav-header .user-profile .el-icon-fa-user-circle {
  font-size: 20px !important;
}
.tool-icon {
  width: 12px;
  height: 100%;
  font-size: 12px;
  margin-right: 10px;
}
.brand {
  float: left;
  height: 100%;
  display: flex;
  align-items: center;
  margin-left: 105px;
}
.brand a {
  color: white;
  font-size: 19px;
  background-color: black;
  padding: 1px 12px;
}
.node-sys-title .split-line {
  font-family: Arial;
}
.node-sys-title .el-dropdown-link,
.node-sys-title .el-icon-arrow-down {
  color: #409EFF;
}
.toolbar {
  height: 100%;
  line-height: 60px;
}
.toolbar:after {
  content: ' ';
  clear: both;
  display: block;
}
.toolbar .left-nav-bar {
  float: left;
}
.toolbar .badge-item {
  margin-top: -28px;
  margin-left: -12px;
}
.nav-link {
  color: black;
  display: inline-block;
  width: 100%;
  text-align: left;
}
.navbar-menu {
  float: right;
  margin-right: 110px;
}
.navbar-menu a {
  color: #000000;
}
.navbar-menu .avatar {
  border-radius: 50%;
  width: 35px;
}
.navbar-menu i {
  font-size: 20px;
}
.navbar-menu .nav-right-menu-item {
  float: left;
  margin: auto 12px;
  font-size: 12px;
  position: relative;
}
.navbar-menu .nav-right-menu-item i {
  color: #666;
}
.navbar-menu .my-profile:hover .my-profile-items {
  display: block;
}
.hover {
  padding: 12px;
}
.hover:hover {
  background-color: #F5F5F5;
}
.center {
  display: flex;
  align-items: center;
}
.my-profile-items {
  background-color: white;
  border: 1px solid #ccc;
  width: 155px;
  line-height: 1;
  font-size: 12px;
  color: #333;
}
.my-profile-items i {
  font-size: 12px;
  margin-right: 10px;
}
.my-profile-items .el-icon-plus {
  cursor: pointer;
}
.my-profile-items .create-node-btn {
  float: right;
  padding-left: 5px;
  width: auto;
}
.my-profile-items .my-node-list {
  margin-bottom: 10px;
}
.my-profile-items .my-profile-item {
  min-height: 20px;
  border-bottom: 1px solid #ccc;
}
.my-profile-items .my-profile-item .node-item {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  color: #666;
  cursor: pointer;
  line-height: 30px;
  padding: 0 18px 0 32px;
}
.page-title {
  margin-left: 18px;
  font-size: 16px;
}
