.main {
  min-height: 100%;
}
.main-view {
  min-height: 100%;
}
.left-sidebar {
  position: fixed;
  top: 60px;
  left: 0;
  bottom: 0;
}
.content {
  margin-top: 60px;
}
.content {
  transition: all 0.5s;
}
.footer-wrap {
  transition: all 0.5s;
}
.collapse-sidebar .content,
.collapse-sidebar .footer-wrap {
  margin-left: 30px;
}
