



































@import "../../styles/mixin.less";

.main {
  min-height: 100%;
}

.main-view {
  min-height: 100%;
}

.left-sidebar {
  position: fixed;
  top: @header-height;
  left: 0;
  bottom: 0;
}

.content {
  margin-top: @header-height;
}

.content {
  transition: all .5s;
}

.footer-wrap {
  transition: all .5s;
}

.collapse-sidebar {

.content, .footer-wrap {
  margin-left: 30px;
}

}
